.loader {
  width: 200px;
  height: 200px;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  align-items: center;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
  top: 300px;
  left: 50%;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 190px;
  height: 190px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

